import { useState, useCallback } from "react";
import { Link } from "react-router-dom";
import "./Navbar.css"; // Import file CSS

function Navbar() {
  const [menuOpen, setMenuOpen] = useState(false);

  // Hàm toggle menu
  const toggleMenu = useCallback(() => setMenuOpen((prev) => !prev), []);

  return (
    <nav className="navbar">
      <div className="navbar-container">
        <h1 className="navbar-logo">My Tech CV</h1>
        
        {/* Icon Menu Mobile */}
        <div className="menu-icon" onClick={toggleMenu}>
          {menuOpen ? "✖" : "☰"}
        </div>

        {/* Menu Chính */}
        <ul className={`navbar-menu ${menuOpen ? "active" : ""}`}>
          {[
            { path: "/", label: "Trang chủ" },
            { path: "/personal-info", label: "Thông tin cá nhân" },
            { path: "/career-goals", label: "Mục tiêu" },
            { path: "/education", label: "Học vấn" },
            { path: "/experience", label: "Kinh nghiệm" },
            { path: "/skills", label: "Kỹ năng" },
            { path: "/certificates", label: "Chứng chỉ" },
            { path: "/projects", label: "Dự án" },
          ].map(({ path, label }) => (
            <li key={path}>
              <Link to={path} onClick={() => setMenuOpen(false)}>
                {label}
              </Link>
            </li>
          ))}
        </ul>
      </div>
    </nav>
  );
}

export default Navbar;
